/* *******  Visible in all viewport widths - Global CSS ******* */
@viewport {
  width: device-width;
  zoom: 1;
}
@-ms-viewport {
  width: device-width;
  zoom: 1;
}
* {
  box-sizing: border-box;
}

/* *******  END ******* */
/* *******  Variables start ******* */
/* *******  END ******* */
/* ******* Global - starts here ******* */
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
button,
body {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
  line-height: 1.6;
  font-family: "Montserrat", "Open Sans", "Arial", sans-serif;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section {
  display: block;
}

html {
  color: #140A17;
  -webkit-text-size-adjust: 100%;
}

h2, .default-font-weight {
  font-weight: normal;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
}

.border-bottom-grey {
  border-bottom: 1px solid #E8E8E9;
}

.fixed-width {
  max-width: 90em;
  margin: 0 auto;
}

.flex {
  display: flex;
}

.donate-btn {
  background-color: #FCB040;
  border: 0;
  border-radius: 8px;
  padding: 16px;
  white-space: nowrap;
  margin-left: 10%;
}

/* *******  Header start ******* */
.linklikst-btn-container {
  align-items: center;
  margin-left: 10%;
}

nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
nav li {
  margin-right: 2em;
}
nav a {
  display: block;
  padding: 6px 12px;
  text-decoration: none;
  color: #140A17;
  text-decoration: none;
}

li.selected {
  font-weight: bold;
}

.updates-container {
  background-color: #7F3F98;
  color: #fff;
  text-transform: uppercase;
  padding: 16px;
  text-align: center;
}

.updates-container a {
  color: #fff;
}

.hero-container {
  background: #090414 url("../images/HeroImage.jpg") no-repeat right 0;
  min-height: 597px;
  display: flex;
  align-items: center;
}

.hero-container h1 {
  margin: 0;
}

.hero-container-content {
  display: flex;
  color: #fff;
  flex-direction: column;
  margin: 12em 0;
  width: 50%;
}

.hero-action-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 6em;
}

.hero-action {
  display: flex;
  border-radius: 16px;
  overflow: auto;
  margin: 0 4em 0 0;
  width: 40%;
}

.hero-action:last-child {
  margin: 0;
}

.hero-action .first {
  background-color: #fff;
  color: #140A17;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 2em;
}

.hero-action .first h1 {
  text-transform: uppercase;
  margin-bottom: 0.5em;
}

.hero-action .last {
  background-color: #B47DC9;
}

.mid-content p, .mid-content h1 {
  text-align: center;
}

.mid-content p {
  margin: 6em 0;
  padding: 1em 6em;
}

.mid-content h1 {
  margin-top: 6em;
}

.book-showcase {
  /* background:conic-gradient($primary-600 15.5%, $primary-300 0% 66.35%, $primary-600 0);*/
  background: conic-gradient(#B47DC9 29.5%, #CBC6E5 0% 79.45%, #B47DC9 0);
}

.book-showcase h2 {
  text-transform: uppercase;
  color: #140A17;
}

.book-showcase h2.second {
  color: #fff;
}

.book-showcase-content {
  display: flex;
  justify-content: center;
  padding: 3em;
}

.book-showcase-content h2.second {
  font-weight: bold;
}

.book-showcase-content a {
  color: #fff;
  text-align: end;
}

.book-showcase-content div {
  display: flex;
  flex-direction: column;
}

.testimonial-container {
  background: linear-gradient(180deg, #fff 35%, #090414 35%);
  text-align: center;
}

.testimonial-container p {
  color: #fff;
  font-size: 2em;
  padding: 2em;
  margin: 0 20%;
}

/* *******  Footer start ******* */
footer .footer-container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 2% auto;
}

footer .left > div {
  flex-direction: column;
}

.footer-top-border {
  border-top: 1px solid #E8E8E9;
  text-align: center;
  padding: 2em 0;
}

footer .center nav ul {
  flex-direction: column;
}

footer .left button {
  width: 50%;
}

footer .right h1 {
  font-size: 1em;
}

body .footer-container p {
  margin-bottom: 0;
}

/* *******  About Us page start ******* */
.about .hero-container-content {
  width: auto;
  align-items: center;
  margin: 6em 0;
  line-height: 1.6;
}

.about .hero-container {
  background-image: none;
  background-color: #090414;
}

.about .hero-container h1 {
  font-size: 7.5em;
  text-transform: uppercase;
}

.about .hero-container p {
  width: 60%;
  text-align: center;
}

.about .mission {
  background: #090414 url("../images/aboutus_mission.jpg") no-repeat center bottom;
  background-size: cover;
  min-height: 728px;
}

.whoweare {
  background-color: #090414;
  position: relative;
  color: #fff;
}

.whoweare h1 {
  margin-top: 0;
  padding-top: 3em;
}

.absolute-circle {
  position: absolute;
  top: 0.5%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 2em solid #000;
  border-radius: 50%;
  width: 100%;
}

.whoweare a {
  color: #fff;
}

.whoweare .whoweare-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-width: 39%;
  width: 39%;
  margin: 0 auto;
}

.mid-content .whoweare-content p {
  margin-bottom: 0;
  margin-top: 2em;
}

.mid-content .whoweare-content p.first {
  margin-top: 0;
}

.mid-content .whoweare-content p.second {
  margin-top: 0;
}

.howstarted .fixed-width {
  display: flex;
  flex-direction: row;
  padding-top: 6em;
}

.mid-content .howstarted p, .mid-content .newdir-content p {
  margin-top: 0;
  text-align: left;
}

.mid-content .howstarted h1, .mid-content .newdir-content h1 {
  margin-top: 0;
  white-space: nowrap;
}

.newdir {
  background-color: #E1CBE9;
}

.newdir-content {
  display: flex;
  padding-top: 6em;
}

.newdir-content img {
  vertical-align: bottom;
}

.social-impact {
  display: flex;
  justify-content: center;
  gap: 10%;
  margin-bottom: 6em;
}

.social-impact div {
  text-align: center;
}

.social-impact p, .social-impact h1 {
  margin: 0;
  padding: 0;
}

.social-impact h1 {
  color: #B47DC9;
}

/* *******  Projects start ******* */
.about.project .mission {
  background: #fff url("../images/All women.png") no-repeat center bottom;
  background-size: cover;
  min-height: 477px;
}

.project p {
  margin-bottom: 2em;
}

.project .updates-container p {
  margin: 0;
}

ul.listwithicon {
  list-style-type: none;
  padding: 1em 6em;
}

.listwithicon li {
  background: url("../images/hand-heart-outline.png") no-repeat top left;
  padding-left: 3em;
  margin-bottom: 2em;
}

.newdir.project {
  background: #F9F8F6 url("../images/Donate.jpg") no-repeat right 0;
}

.newdir.project .newdir-content div {
  width: 39em;
}

.mid-content .project .newdir-content h1 {
  text-align: left;
}

.mid-content .project .newdir-content p {
  padding-left: 0;
  padding-right: 0;
}

.support-work {
  padding-bottom: 4em;
}

.support-work p {
  margin-bottom: 0;
}

/* *******  Current Projects start ******* */
.current.project {
  background: linear-gradient(0deg, #561644 0%, #834396 100%);
}

.quote-row {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 6em;
}

.quote {
  display: inline-flex;
  padding: 48px 40px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  border-radius: 100px;
  background: #FFF;
  box-shadow: 0px 6px 6px 0px rgba(0, 0, 0, 0.25);
}

.quote-content {
  display: flex;
  flex-direction: column;
  width: 495px;
  padding: 80px 48px 64px 48px;
  align-items: flex-start;
  gap: 40px;
  border-radius: 48px;
  background: #fff;
}

.quote-content h1, .quote-content p {
  margin: 0;
}

.quote-content p {
  padding: 0;
}

.quote-content h1 {
  font-size: 1.25em;
  font-weight: normal;
}

.quote1, .quote2, .quote3, .quote4 {
  text-align: center;
}